.columns {
  margin: 1em 0;
  min-width: 100%; }
  .columns:not(.columns-liquid) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  .columns.columns-liquid {
    column-gap: 2em; }
  .columns.columns-1 .column {
    width: calc((100% - 0em) / 1); }
  .columns.columns-1.columns-liquid {
    column-count: 1; }
  .columns.columns-2 .column {
    width: calc((100% - 1em) / 2); }
  .columns.columns-2.columns-liquid {
    column-count: 2; }
  .columns.columns-3 .column {
    width: calc((100% - 2em) / 3); }
  .columns.columns-3.columns-liquid {
    column-count: 3; }
  .columns.columns-4 .column {
    width: calc((100% - 3em) / 4); }
  .columns.columns-4.columns-liquid {
    column-count: 4; }
  .columns.columns-5 .column {
    width: calc((100% - 4em) / 5); }
  .columns.columns-5.columns-liquid {
    column-count: 5; }
  .columns.columns-6 .column {
    width: calc((100% - 5em) / 6); }
  .columns.columns-6.columns-liquid {
    column-count: 6; }
  .columns.columns-7 .column {
    width: calc((100% - 6em) / 7); }
  .columns.columns-7.columns-liquid {
    column-count: 7; }
  .columns.columns-8 .column {
    width: calc((100% - 7em) / 8); }
  .columns.columns-8.columns-liquid {
    column-count: 8; }
  .columns.columns-9 .column {
    width: calc((100% - 8em) / 9); }
  .columns.columns-9.columns-liquid {
    column-count: 9; }
  .columns > .column-2.column-narrow {
    width: calc(33.33333% - .5em); }
  .columns > .column-2.column-wide {
    width: calc(66.66666% - .5em); }
